.modal__container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);

  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.modal__note-information {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  padding: 32px;
  width: 100%;
  max-width: 340px;
  background-color: peachpuff;
  border-radius: 10px;
  font-size: 1em;
}

.modal__input {
  font-size: 1em;
}

/* buttons */
.modal__button-container {
  display: flex;
  justify-content: space-around;
}

.modal__ok-button,
.modal__cancel-button {
  margin: 0.5em;
  padding: 0.2em;
  width: 70px;
}
